import React from 'react';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import withStyles from '@material-ui/core/styles/withStyles';
import Typography from '@material-ui/core/Typography';
import { StaticQuery } from 'gatsby';

const styles = theme => ({
  container: {
    paddingTop: theme.spacing.unit * 3
  },
  paper: {
    // padding: `${theme.spacing.unit} ${theme.spacing.unit*2}`,
    width: 400,
    height: 200
  }
});

const Principles = ({ classes }) => {
  return (
    <StaticQuery
      query={graphql`
      query PrincipleQuery {
        dataJson {
          developer {
            principles {
              title
              content
            }
          }
        }
      }
    `}
      render={(data) => {
        const { principles } = data.dataJson.developer;
        console.log({ principles })
        return (
          <Grid className={classes.container} container>
            <Grid item xs={12}>
              <Grid container justify="center" spacing={Number(32)}>
                {principles.map(principle => (
                  <Grid key={principle.title} item>
                    <Paper className={classes.paper}>
                      <Typography variant="headline" gutterBottom>
                        {principle.title}
                      </Typography>
                      <Typography component="p">
                        {principle.content}
                      </Typography>
                    </Paper>
                  </Grid>
                ))}
              </Grid>
            </Grid>
          </Grid>
        )
      }}
    />
  );
};

export default withStyles(styles)(Principles);