import React from 'react';

import PropTypes from 'prop-types';
import ImageHorizontalGrid from '../../components/ImageHorizontalGrid';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { StaticQuery } from 'gatsby';

const styles = theme => ({
  container: {
    paddingTop: theme.spacing.unit * 2
  }
});

const Skills = ({ classes }) => {
  return (
    <StaticQuery
      query={graphql`
      query SkillsQuery {
        dataJson {
          developer {
            skills {
              name
              smallImage: image {
                childImageSharp {
                  small: fixed(width: 150, height: 150) {
                    ...GatsbyImageSharpFixed
                  }
                }
              }
            }
          }
        }
      }
    `}
      render={(data) => {
        const { skills } = data.dataJson.developer;
        return (
          <div className={classes.container}>
            <ImageHorizontalGrid items={skills} />
            <Typography variant="caption" gutterBottom>
              Some of the frameworks, libraries and services I'm familiar with
            </Typography>
          </div>
        )
      }}
    />
  );
};

export default withStyles(styles)(Skills);